<template>
  <div>
    <b-container fluid :style="cssVars">
      <b-row>
        <b-col class="p-5 header-inicio" style="">
          <h2>Ultimas Noticias</h2>
        </b-col>
      </b-row>
    </b-container>

    <b-container :style="cssVars">
      <b-row class="py-5">
        <b-col
          v-for="blog in blogs.data.data.blogs"
          :key="blog.id"
          class="col-sm-6 col-md-4 p-2"
        >
          <div class="card">
            <img
              v-if="blog.image.substr(0, 4) == 'http'"
              :src="blog.image"
              style="width: 100%; height: 15em"
            />
            <img
              v-else
              :src="url + blog.image"
              style="width: 100%; height: 15em"
            />
            <div class="container-card">
              <h4>
                <b>{{ blog.titulo.substr(0, 49) + "..." }}</b>
              </h4>
              <p class="">{{ blog.descripcion.substr(0, 79) }}</p>
              <router-link
                class="nav-link"
                :to="{
                  name: 'detalleblog',
                  params: { id: blog.id },
                }"
              >
                <b-button class="w-100" variant="outline-naranja my-3"
                  >Ver mas
                </b-button>
              </router-link>
            </div>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import { URL_LOCAL } from "../config.js";

export default {
  data() {
    return {
      url: URL_LOCAL + "storage/",
      form: {
        desde: "",
        hasta: "",
        habitaciones: "",
        banos: "",
        estacionamiento: "",
        tipo: null,
        estrato: null,
        tipos: [
          { value: "1", text: "Casa" },
          { value: "2", text: "Apartamento" },
          { value: "3", text: "Terreno" },
          { value: "4", text: "Local", disabled: true },
        ],
        estratos: [
          { value: "1", text: "1" },
          { value: "2", text: "2" },
          { value: "3", text: "3" },
          { value: "4", text: "4" },
          { value: "5", text: "5" },
          { value: "6", text: "6" },
        ],
      },
    };
  },
  slide: 0,
  sliding: null,
  states: [
    { state: "Bogota" },
    { state: "Cucuta" },
    { state: "Medellin" },
    { state: "Cali" },
  ],
  props: {
    msg: String,
  },

  computed: {
    inmuebles() {
      return this.$store.state.inmuebles;
    },
    usuario() {
      return this.$store.state.usuario;
    },

    blogs() {
      return this.$store.state.blogs;
    },

    banners() {
      return this.$store.state.banners;
    },

    code() {
      return this.$store.state.code;
    },
    cssVars() {
      return {
        "--bg-color-primario": this.$store.state.color.primario,
        "--bg-color-secundario": this.$store.state.color.secundario,
      };
    },
  },

  created() {
    this.$store
      .dispatch(
        "getCode",
        window.location.href.split("//")[1].split(".")[0].split(":")[0]
      )
      .then(() => (this.loading = false));

    // this.getInmuebles();

    this.$store
      .dispatch("getInmuebles", this.code)
      .then(() => this.setTitulo());

    this.$store
      .dispatch("getBlogs", this.code)
      .then(() => (this.loading = false));

    this.$store
      .dispatch("getBanners", this.code)
      .then(() => (this.loading = false));
  },

  methods: {
    onSlideStart(slide) {
      this.sliding = true;
      this.slide = slide;
    },
    onSlideEnd(slide) {
      this.sliding = false;
      this.slide = slide;
    },

    setTitulo() {
      document.title = this.$store.state.configuracion.nombre;
      // document.getElementById('pageFavicon').attr('href', 'https://res.cloudinary.com/dm5nedbyd/image/upload/c_fill,h_32,w_32/v1644445047/ixgp6fzzqbvhbmbxclhd.webp');
      const favicon = document.getElementById("pageFavicon");

      console.log(this.$store.state.configuracion.logo)

      if (this.$store.state.configuracion.logo.substr(0, 4) == "http") {
        favicon.href =
          this.$store.state.configuracion.logo.split("upload")[0] +
          "upload/c_fill,h_32,w_32" +
          this.$store.state.configuracion.logo.split("upload")[1];
      } else {
        favicon.href = this.url + this.$store.state.configuracion.logo;
      }
    },
  },
};
</script>

