  background: var(--bg-color-primario);
  color: #fff;
  padding-top: 0.3em;
  padding-bottom: 0.3em;
  font-size: 0.8em;
}

.cintillo .col {
  border-right: 1px solid #fff;
}

.form-inline,
.form-inline .form-control {
  display: inline-block;
  width: 80%;
}

.col-buscar .form-inline .form-control {
  width: 80%;
}
.cintillo .col a {
  color: #fff;
}

.cintillo .col .b-icon {
  margin-left: 1em;
}
.card-title {
  margin-bottom: 0.5rem;
  text-align: left;
  font-weight: 600;
  line-height: 1;
}

.card-text {
  text-align: left;
}
.card-text p {
  text-align: left;
  margin-bottom: 0;
}

.ubicaciones {
  background: #333;
  color: #fff;
  min-height: 10em;
  padding-bottom: 3em;
  padding-top: 3em;
}

.footer {
  background: var(--bg-color-primario);
  color: #fff;
  min-height: 10em;
  padding-bottom: 3em;
  padding-top: 3em;
  text-align: left;
}

.footer-des {
  text-align: left;
}

.card {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
