  padding: 3em;
  background: var(--bg-color-secundario);
}

.btn-buscar,
.btn-buscar:focus {
  color: #fff;
  background-color: var(--bg-color-primario);
  border-color: var(--bg-color-primario);
  box-shadow: 0 0 0 0.25rem rgb(49 132 253 / 50%);
}

.mh-heading--bottom-separator:after {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 90px;
  width: 50%;
