  height: 2px;
  background: var(--bg-color-primario);
}

.mh-heading--bottom-separator {
  position: relative;
  text-transform: uppercase;
  text-align: center;
  padding-bottom: 12px;
  margin-bottom: 36px;
}

.detalle,
.ubicacion {
  font-size: 0.8em;
  line-height: 1;
  height: 2em;
}

.precio {
  color: var(--bg-color-primario);
  font-weight: 600;
  font-size: 1.3em;
}

.bnew {
  position: absolute;
  top: 1em;
  background: var(--bg-color-primario);
  padding-left: 1em;
  padding-right: 1em;
  font-size: 1em;
}

.bsale {
  position: absolute;
  top: 1em;
  right: 0;
  background: var(--bg-color-primario);
  padding-left: 1em;
  padding-right: 1em;
  font-size: 1em;
}

.btn-outline-naranja {
  color: var(--bg-color-primario);
  border: 3px solid var(--bg-color-primario);
  text-transform: uppercase;
  font-weight: 600;
  border-radius: 0;
}

.btn-outline-naranja:focus {
  box-shadow: 0 0 0 0.25rem rgb(242 86 27 / 15%);
}

.btn-outline-naranja:hover {
  color: var(--bg-color-primario);
}

.btn-naranja {
  color: #fff;
  background-color: var(--bg-color-primario);
  border-color: var(--bg-color-primario);
  border-radius: 0;
}

.btn-outline-blanco:hover {
  color: #fff;
  background-color: #fff;
  border-color: #fff;
}
.btn-outline-primary {
  color: #fff;
  border-color: #fff;
}

.outline-blanco:focus {
  box-shadow: 0 0 0 0.25rem rgb(255 255 255 / 34%);
}

.rowinput {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  text-align: left;
}

.rowinput label,
.rowinput input,
.rowinput select {
  width: 100%;
  font-size: 0.8em;
}

select {
  padding: 0.5em;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.header-inicio {
  background: var(--bg-color-secundario);
}

.container-card h4 {
  height: 2.5em;
}

.enlaceubicacion {
  width: 100%;
  text-align: left;
  padding: 0;
  margin: 0;
  text-decoration: none;
  color: #333;
}

button.close {
  background: transparent;
  border: none;
  float: right;
  font-size: 1.5em;
  line-height: 1;
}
</style>
